___CSS_LOADER_EXPORT___.push([module.id, `.report-container {
    padding: 30px;
    max-width: 600px; /* 최대 너비를 제한하여 버튼이 화면을 넘어가지 않도록 설정 */
    margin: 20px auto;
    background-color: #28293d;
    border-radius: 10px;
    color: white;
  }
  
  .report-title {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .report-section {
    margin-bottom: 20px;
  }
  
  .report-section label {
    display: block;
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  .report-section button,
  .report-section input[type="file"] {
    padding: 12px 20px;
    font-size: 16px;
    background-color: #0066cc;
    border: none;
    border-radius: 5px;
    color: white;
    cursor: pointer;
    width: 100%; /* 버튼의 너비를 100%로 설정 */
    max-width: 100%; /* 최대 너비를 100%로 제한 */
    margin-bottom: 10px;
  }
  
  .report-section input[type="text"],
  .report-section textarea {
    width: 100%;
    padding: 12px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #333;
    color: white;
    margin-bottom: 10px;
  }
  
  .report-section input[type="text"]::placeholder,
  .report-section textarea::placeholder {
    color: #999;
  }
  
  .report-submit {
    display: flex;
    justify-content: center;
  }
  
  .report-submit button {
    padding: 15px 30px;
    font-size: 18px;
    background-color: #0066cc;
    border: none;
    border-radius: 5px;
    color: white;
    cursor: pointer;
    width: 100%; /* 신고하기 버튼의 너비를 부모 요소에 맞게 조정 */
    max-width: 300px; /* 최대 너비를 300px로 설정하여 화면을 넘어가지 않도록 설정 */
  }
  
  .report-submit button:hover {
    background-color: #004c99;
  }
  
  .location-info {
    margin-bottom: 10px;
    color: #ff6666;
  }
